import React from 'react';
import ReactDOM from 'react-dom/client';
import {HashRouter as Router,Routes, Route, Link} from 'react-router-dom';
//import {BrowserRouter as Router,Routes, Route, Link} from 'react-router-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';

const root = ReactDOM.createRoot(document.getElementById('root'));
let defaultTopic = 'rcnn'
if (sessionStorage.getItem('topic')) {defaultTopic = sessionStorage.getItem('topic')}
const data      = require('./config.json')
const keys = Object.keys(data.topics)
const urls = keys.map((item,index)=> (<Route path={"/"+item+"/"} element={<App topic={item}/>}/>))
//for (const key in Object.keys(data.topics])){
//    keys.push(key)
    
//}
console.log(defaultTopic)
root.render(
  <React.StrictMode> 
  <Router>
    <Routes>
    {urls}
    <Route path="/" element={<App topic={'rcnn'}/>} />
    </Routes>
    </Router>
  </React.StrictMode>
);


