//https://github.com/apps/utterances
import React from 'react'
import {useEffect} from 'react'


function Comments() {
  const commentBox = React.createRef();
  useEffect(() => {
    const commentScript = document.createElement('script')
    commentScript.async = true
    commentScript.src = 'https://utteranc.es/client.js'
    commentScript.setAttribute('repo', 'irfanhasib0/blogs')
    commentScript.setAttribute('issue-term', 'url')
    commentScript.setAttribute('id', 'utterances')
    commentScript.setAttribute('label', 'comment')
    commentScript.setAttribute('theme', 'github-light')
    commentScript.setAttribute('crossorigin', 'anonymous')
    if (commentBox && commentBox.current) {
      commentBox.current.appendChild(commentScript)
    } else {
      console.log(`Error adding utterances comments on: ${commentBox}`)
    }
    const removeScript = () => {
      commentScript.remove();
      document.querySelectorAll(".utterances").forEach(el => el.remove());
    };
    return () => {
      removeScript();
    };
  }, [])
  
  return (
    <>
      <div ref={commentBox}> </div>
    </>
  )
}
export default Comments;
